import React, { useMemo, useState, useCallback } from 'react';
import styled from 'styled-components';
import { _ } from '@splunk/ui-utils/i18n';
import get from 'lodash/get';
import { noop } from 'lodash';
import { SortableHandle } from 'react-sortable-hoc';
import Remove from '@splunk/react-icons/Remove';
import Warning from '@splunk/react-icons/Warning';
import type { SVGPropsOptionalViewBox } from '@splunk/react-icons/SVG';
import Tooltip from '@splunk/react-ui/Tooltip';
import Clickable from '@splunk/react-ui/Clickable';
import { useFeatureFlags } from '@splunk/dashboard-context';
import { variables, pick, pickVariant } from '@splunk/themes';
import {
    formatRiskyCommandErrorMessage,
    isRiskyCommandError,
} from '@splunk/dashboard-utils/riskyCommandUtils';
import RiskyWarningPopover from '@splunk/dashboard-ui/RiskyWarningPopover';
import OptionalTooltip from '@splunk/dashboard-ui/utils/OptionalTooltip';
import { NoResult } from '@splunk/dashboard-icons';

import { InputProps, HOCProps, InputStaticProps } from '../types';
import { getSearchStatus } from './inputItem';

interface InputContainerProps {
    showSelection: boolean;
    mode: 'edit' | 'view';
    width: number;
}

const selectedInputBackgroundColor = {
    enterprise: {
        light: variables.gray92,
        dark: variables.gray30,
    },
    prisma: variables.backgroundColorPopup,
};

export const InputContainer = styled.div.attrs({
    'data-test': 'input-container',
})<InputContainerProps>`
    width: ${(props) => props.width}px;
    padding: 8px;
    border-radius: 4px;
    border: 1px solid;
    position: relative;
    background-color: ${pickVariant<InputContainerProps>('showSelection', {
        true: selectedInputBackgroundColor,
        false: 'transparent',
    })};
    border-color: ${pickVariant<InputContainerProps>('showSelection', {
        true: {
            enterprise: variables.accentColorL10,
            prisma: variables.interactiveColorPrimary,
        },
        false: 'transparent',
    })};
    &:hover {
        background-color: ${pickVariant<InputContainerProps>('mode', {
            edit: selectedInputBackgroundColor,
            view: 'transparent',
        })};
    }
`;

const InputTitleRemoveContainer = styled.div.attrs({
    'data-test': 'input-title-remove-container',
})`
    display: flex;
    margin-bottom: 4px;
    align-items: center;
`;

const InputTitle = styled.div.attrs({
    'data-test': 'input-title',
})`
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    font-size: 13px;
    cursor: default;
    color: ${variables.contentColorActive};
    min-height: ${(props) => (props.title ? null : variables.lineHeight)};
    flex-grow: 0;
`;

interface InputRemoveProps {
    isSelected: boolean;
}

const InputRemove = styled.div.attrs({
    'data-test': 'input-remove',
})<InputRemoveProps>`
    height: 16px;
    margin-left: 8px;
    visibility: ${(props) => (props.isSelected ? 'visible' : 'hidden')};
    color: ${variables.contentColorMuted};
    &:hover {
        cursor: pointer;
    }
    display: flex;
    align-items: center;
    flex-direction: row-reverse;
    flex-grow: 0;
`;

const InputDragHandle = styled.div.attrs({ 'data-test': 'input-drag-handle' })`
    display: none;
    position: absolute;
    left: 0;
    right: 0;
    margin: 0 auto;
    width: 30px;
    height: 15px;
    /* background gradient dots */
    background: ${variables.draggableBackground};
    /* blurred background */
    background-color: ${pick({
        enterprise: {
            light: 'rgba(184, 184, 184, 0.3)',
            dark: 'rgba(35, 36, 43, 0.2)',
        },
        prisma: {
            light: 'rgba(184, 184, 184, 0.3)',
            dark: 'rgba(35, 36, 43, 0.8)',
        },
    })};
    cursor: move;

    ${InputContainer}:hover & {
        display: block;
    }
`;

const SortableDragHandle = SortableHandle(InputDragHandle);
SortableDragHandle.displayName = 'SortableDragHandle';

const tooltipStyle = {
    height: '16px',
};

interface InputStatusContainerProps {
    'data-test': string;
    icon: (props: SVGPropsOptionalViewBox) => JSX.Element;
    statusMessage: string;
    statusType: string;
}

const InputStatus = styled.div`
    height: 16px;
    margin-left: 8px;
    display: flex;
    align-items: center;
    /* Hack to make this align because we can't style the tooltip toggle properly, and it is pushing content down */
    margin-top: -2px;
    flex-grow: 0;
`;

const InputStatusIcon = styled(Clickable)`
    height: 16px;
    color: ${pickVariant<Pick<InputStatusContainerProps, 'statusType'>>(
        'statusType',
        {
            error: variables.accentColorNegative,
            warning: variables.statusColorMedium,
        }
    )};
    display: flex;
    align-items: center;
    flex-grow: 0;
    cursor: 'pointer';
`;

const InputStatusContainer = ({
    'data-test': dataTest,
    icon: Icon,
    statusMessage,
    statusType,
}: InputStatusContainerProps): JSX.Element => (
    <InputStatus data-test={dataTest}>
        <Tooltip content={statusMessage} style={tooltipStyle}>
            <InputStatusIcon
                data-test={`${dataTest}-icon`}
                statusType={statusType}
            >
                <Icon size="11px" hideDefaultTooltip />
            </InputStatusIcon>
        </Tooltip>
    </InputStatus>
);

const Spacer = styled.div`
    display: flex;
    flex-grow: 1;
`;

const empty = {};

type HOC = (props: HOCProps) => React.ReactElement<HOCProps>;

/**
 * HOC that wraps the Input component in a container displaying input title, error icon if error is present
 * and remove button if selected in edit mode. Also sets container style (backgroundColor, border) based on mode and isSelected
 * @param {ReactElement} InputComponent Input Component
 * @param {String} mode Dashboard mode - edit or view
 * @param {String} title Input title
 * @param {String} id Input id
 * @param {Object} options Input options
 * @param {Boolean} isSelected Input is selected in edit mode or not
 * @param {Boolean} isError Input has an error
 * @param {String} errorMessage Input error message
 * @param {Number} width Input Container width - TRP would override this
 */
export const withInputWrapper = (
    InputComponent: React.ComponentType<InputProps> & InputStaticProps
): HOC => {
    const WrappedComponent = ({
        mode = 'view',
        title,
        id,
        options = empty,
        isSelected = false,
        isError = false,
        errorMessage = '',
        width = 180,
        onRemove = noop,
        dataSources = empty,
        ...rest
    }: HOCProps): React.ReactElement => {
        const [currentWidth, setWidth] = useState(width);
        const { enableDragDropInputs } = useFeatureFlags();

        const searchHasNoResults = getSearchStatus(dataSources);

        const titleMemo = useMemo(() => {
            const displayTitle = title || '';
            return <InputTitle title={displayTitle}>{displayTitle}</InputTitle>;
        }, [title]);

        const handleRemove = useCallback(() => onRemove(id), [onRemove, id]);

        const removeMemo = useMemo(() => {
            if (mode !== 'edit') {
                return null;
            }
            return (
                <InputRemove onClick={handleRemove} isSelected={isSelected}>
                    <Remove size="11px" />
                </InputRemove>
            );
        }, [mode, handleRemove, isSelected]);

        const search = get(dataSources, 'primary.meta.search');

        const statusMemo = useMemo(() => {
            if (!isError && !searchHasNoResults) {
                return null;
            }

            const isRiskyCommand = isRiskyCommandError(errorMessage);

            if (isRiskyCommand) {
                const tooltipMessage = formatRiskyCommandErrorMessage(
                    errorMessage
                );
                return (
                    <OptionalTooltip message={tooltipMessage}>
                        <RiskyWarningPopover
                            statusIcon={
                                <InputStatusIcon
                                    data-test="risky-error"
                                    statusType="error"
                                    tabIndex={0}
                                >
                                    <Warning size="11px" hideDefaultTooltip />
                                </InputStatusIcon>
                            }
                            riskySearch={search}
                            id={id}
                            message={errorMessage}
                            type="input"
                        />
                    </OptionalTooltip>
                );
            }

            return isError ? (
                <InputStatusContainer
                    data-test="input-error"
                    statusMessage={errorMessage}
                    icon={Warning}
                    statusType="error"
                />
            ) : (
                <InputStatusContainer
                    data-test="input-search"
                    statusMessage={_('No search results returned')}
                    icon={NoResult}
                    statusType="warning"
                />
            );
        }, [isError, searchHasNoResults, errorMessage, search, id]);

        const dragHandleEl = useMemo(
            () =>
                enableDragDropInputs && mode === 'edit' && isSelected ? (
                    <SortableDragHandle />
                ) : null,
            [mode, isSelected, enableDragDropInputs]
        );

        return (
            <InputContainer
                mode={mode}
                width={currentWidth}
                showSelection={isSelected && mode === 'edit'}
            >
                {dragHandleEl}
                <InputTitleRemoveContainer>
                    {titleMemo}
                    {statusMemo}
                    <Spacer />
                    {removeMemo}
                </InputTitleRemoveContainer>
                <InputComponent
                    id={id}
                    isSelected={isSelected}
                    options={options}
                    isError={isError}
                    errorMessage={errorMessage}
                    onUpdateWidth={setWidth}
                    dataSources={dataSources}
                    {...rest}
                />
            </InputContainer>
        );
    };
    WrappedComponent.valueToTokens = InputComponent.valueToTokens;
    WrappedComponent.tokensToValue = InputComponent.tokensToValue;
    WrappedComponent.validate = InputComponent.validate;
    WrappedComponent.config = InputComponent.config;
    return WrappedComponent;
};
